import React from 'react';
import '../Navigation/Navigation.css';

class SignIn extends React.Component {
    constructor(){
        super();
        this.state = {
            signInEmail: '',
            signInPassword: '',
            wrongPassword: ''
        }
    }

    onEmailChange = (event) => {
        this.setState({signInEmail: event.target.value});
    }

    onPasswordChange = (event) => {
        this.setState({signInPassword:event.target.value})
    }

    onSubmitSignIn = () => {
        fetch(`https://secure-anchorage-68689.herokuapp.com/signin`, {
            method: 'post',
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({
                email: this.state.signInEmail,
                password: this.state.signInPassword
            })
        })
        .then(response=> response.json())
        .then(user => {
            if (user.id){
                this.props.loadUser(user);
                this.props.onRouteChange('home');
            }else {
                this.setState({wrongPassword : 'Unable to Login'})
            }
        })
    }

    render(){
        const {onRouteChange} = this.props;
        return (
            <div>
            <article className="br3 ba formWindow b--black-10 mv4 w-100 w-50-m w-25-l mw6 shadow-5 center">
                <main className="pa4 black-80">
                    <div className="measure">
                        <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
                        <legend className="f1 fw6 ph0 mh0">Sign In</legend>
                        <div className="mt3">
                            <label className="db fw6 lh-copy f4" htmlFor="email-address">Email</label>
                            <input 
                            className="pa2 input-reset ba b--black bg-transparent hover-bg-black hover-white w-100" 
                            type="email" 
                            name="email-address"  
                            id="email-address"
                            onChange = {this.onEmailChange}
                            />
                        </div>
                        <div className="mv3">
                            <label className="db fw6 lh-copy f4" htmlFor="password">Password</label>
                            <input className="b pa2 input-reset ba b--black bg-transparent hover-bg-black hover-white w-100" 
                            type="password" 
                            name="password"  
                            id="password"
                            onChange = {this.onPasswordChange}
                            />
                        </div>
                       </fieldset>
                        <div className="">
                        <input 
                            onClick = {this.onSubmitSignIn}
                            className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f5 dib" 
                            type="submit" 
                            value="Sign in"
                        />
                        </div>
                        <div className="lh-copy mt3">
                        <p onClick = {() => onRouteChange('register')} className="f5 link dim black db pointer">Register</p>
                        </div>
                    </div>
                </main>
            </article>
            <div>{this.state.wrongPassword}</div>
            </div>
        );
    }
    
}


export default SignIn;